import { yupResolver } from '@hookform/resolvers/yup'
import {
  Box,
  Container,
  FormGroup,
  RadioGroup,
  Typography
} from '@mui/material'
import { DatePicker } from '@mui/x-date-pickers/DatePicker'
import axios from 'axios'
import { Fragment, useEffect, useState } from 'react'
import { Controller, ErrorOption, useForm } from 'react-hook-form'
import { useNavigate } from 'react-router-dom'
import * as yup from 'yup'
import AutoComplete from '../components/autoComplete'
import CheckBox from '../components/checkbox'
import Input from '../components/input'
import Radio from '../components/radio'
import { StepperRegister } from '../components/steperRegister'
import Upload from '../components/upload'
import { PROVINCE_DB } from '../constants/api'
import { useSweet } from '../hooks/sweet'
import { BixUser } from '../models/bixUser'
import { authService } from '../services/authService'
import { useAppSelector } from '../states/hooks'
import { IProvince } from '../type'

const schema = yup.object({
  personType: yup.string(),
  company: yup.object().when('personType', {
    is: 'company',
    then: yup.object({
      comID: yup
        .string()
        .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
        .required('ใส่เลขประจำตัวผู้เสียภาษีอากร'),
      name: yup.string().required('ใส่ชื่อบริษัท'),
      address: yup.string().required('ใส่สถานที่ติดต่อ'),
      province: yup.string().nullable().required('เลือกจังหวัด'),
      tumbol: yup.string().nullable().required('เลือกตำบล/แขวง'),
      ampher: yup.string().nullable().required('เลือกอำเภอ/เขต'),
      postCode: yup.string().nullable().required('เลือกรหัสไปรษณีย์'),
      web: yup.string().required('ใส่ชื่อเว็บไซต์'),
      tel: yup.string().required('ใส่เบอร์โทรศัพท์'),
      email: yup.string().email('รูปแบบ Email ไม่ถูกต้อง').required('ใส่ Email')
    })
  }),
  personCardID: yup
    .string()
    .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
    .required('ใส่เลขประจำตัวประชาชน'),
  name: yup.string().required('ใส่ชื่อ'),
  lastName: yup.string().required('ใส่นามสกุล'),
  birthDay: yup.string().required('ใส่วันเดือนปีเกิด'),
  tel: yup.string().required('ใส่เบอร์โทรศัพท์'),
  email: yup.string().email('รูปแบบ Email ไม่ถูกต้อง').required('ใส่ Email'),
  address: yup.string().required('ใส่สถานที่ติดต่อ'),
  province: yup.string().nullable().required('เลือกจังหวัด'),
  tumbol: yup.string().nullable().required('เลือกตำบล/แขวง'),
  ampher: yup.string().nullable().required('เลือกอำเภอ/เขต'),
  postCode: yup.string().nullable().required('เลือกรหัสไปรษณีย์')
})

const RegisterPage = () => {
  console.log('++++++++++ register +++++++++++++')
  const { fbUser, user } = useAppSelector((state) => state.auth)
  const [step, setStep] = useState(0)
  const email = localStorage.getItem('email')

  const defaultValues: BixUser = {
    type: 'employee',
    personType: 'person',
    jobType: {
      design: true,
      consult: false,
      construct: false
    },
    personCardID: '',
    name: '',
    lastName: '',
    birthDay: '',
    tel: '',
    email: email ?? '',
    gender: 'male',
    address: '',
    province: '',
    tumbol: '',
    ampher: '',
    postCode: '',
    personDocUrl: '',
    personWithDocUrl: ''
    // company: {
    //   comID: '1234',
    //   name: 'ddd',
    //   address: '20/5',
    //   province: 'นนทบุรี',
    //   tumbol: 'บางกรวย',
    //   ampher: 'อำเภอบางกรวย',
    //   postCode: '11130',
    //   location: new GeoPoint(0, 0),
    //   web: 'www',
    //   tel: '1234',
    //   email: 'kkk@mail.com',
    //   companyCerUrl: '',
    //   memorandumUrl: ''
    // }
  }

  const {
    handleSubmit,
    control,
    setValue,
    watch,
    getValues,
    formState: { isValid },
    reset,
    trigger,
    setError
  } = useForm<BixUser>({
    resolver: yupResolver(schema),
    mode: 'all',
    defaultValues
  })

  const zipCode = watch('postCode')
  const zipCodeCom = watch('company.postCode')
  const personType = watch('personType')

  const [provinceList, setProvinceList] = useState<IProvince[]>([])
  const { alertError } = useSweet()
  const navigate = useNavigate()

  const getProvinceData = async () => {
    const res = await axios(PROVINCE_DB)
    setProvinceList(res.data)
  }

  useEffect(() => {
    if (fbUser && user) {
      navigate('/')
    }
    getProvinceData()
  }, [fbUser, navigate, user])

  const onSubmit = async (data: BixUser) => {
    console.log(data)

    if (!data.personDocUrl || !data.personWithDocUrl) {
      alertError({ title: 'Fail', text: 'อัพโหลดบัตรและรูปถ่ายคู่บัตรประชาชน' })
      return
    }
    try {
      if (fbUser) {
        const res: any = await authService.addUser(data)
        console.log(res)
      }
    } catch (err: any) {
      alertError({ title: 'Fail', text: err })
      console.log({ err })
    }
  }

  const checkValid = (): boolean => {
    if (step === 1) {
      return isValid
    }
    return true
  }

  return (
    <Container>
      <Box pb={2}>
        <Typography variant="h5">ลงทะเบียนผู้ใช้</Typography>
      </Box>
      <form onSubmit={handleSubmit(onSubmit)}>
        <StepperRegister
          isValid={checkValid()}
          step1={
            <Fragment>
              <Controller
                name="type"
                control={control}
                render={({ field: { value, ref, ...rest } }) => (
                  <RadioGroup row value={value}>
                    <Radio
                      {...rest}
                      label="ผู้รับจ้าง"
                      inputRef={ref}
                      value="employee"
                    />
                    <Radio
                      {...rest}
                      label="ผู้จ้าง"
                      inputRef={ref}
                      value="employer"
                    />
                  </RadioGroup>
                )}
              />
              <Controller
                name="personType"
                control={control}
                render={({ field: { value, ref, ...rest } }) => (
                  <RadioGroup row value={value}>
                    <Radio
                      {...rest}
                      label="บุคคลธรรมดา"
                      inputRef={ref}
                      value="person"
                    />
                    <Radio
                      {...rest}
                      label="นิติบุคคล"
                      inputRef={ref}
                      value="company"
                    />
                  </RadioGroup>
                )}
              />
            </Fragment>
          }
          step2={
            <Box width="600px">
              <FormGroup row>
                <Controller
                  name="jobType.design"
                  control={control}
                  render={({ field: { ref, value, ...rest } }) => (
                    <CheckBox
                      {...rest}
                      label="ผู้รับจ้างออกแบบ"
                      inputRef={ref}
                      checked={value}
                    />
                  )}
                />
                <Controller
                  name="jobType.consult"
                  control={control}
                  render={({ field: { ref, value, ...rest } }) => (
                    <CheckBox
                      {...rest}
                      label="ผู้รับจ้างที่ปรึกษา"
                      inputRef={ref}
                      checked={value}
                    />
                  )}
                />
                <Controller
                  name="jobType.construct"
                  control={control}
                  render={({ field: { ref, value, ...rest } }) => (
                    <CheckBox
                      {...rest}
                      label="ผู้รับจ้างก่อสร้าง"
                      inputRef={ref}
                      checked={value}
                    />
                  )}
                />
              </FormGroup>
              {personType == 'company' && (
                <Fragment>
                  <Box pt={4}>
                    <Typography variant="h5">ข้อมูลนิติบุคคล</Typography>
                  </Box>
                  <Controller
                    name="company.comID"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="เลขประจำตัวผู้เสียภาษีอากร"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                  <Controller
                    name="company.name"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="ชื่อบริษัท"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                  <Controller
                    name="company.address"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <Input
                        {...rest}
                        label="ที่อยู่ปัจจุบัน"
                        minRows={3}
                        multiline
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.postCode"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="รหัสไปรษณีย์"
                        options={[
                          ...new Set(provinceList.map((e) => e.PostCodeMain))
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.postCode', v)
                          setValue('company.province', '')
                          setValue('company.ampher', '')
                          setValue('company.tumbol', '')
                        }}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.province"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="จังหวัด"
                        options={[
                          ...new Set(
                            provinceList
                              .filter((e) => e.PostCodeMain === zipCodeCom)
                              .map((e) => e.ProvinceThai)
                          )
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.province', v)
                        }}
                        value={value}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.ampher"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="อำเภอ/เขต"
                        options={[
                          ...new Set(
                            provinceList
                              .filter((e) => e.PostCodeMain === zipCodeCom)
                              .map((e) => e.DistrictThai)
                          )
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.ampher', v)
                        }}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.tumbol"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <AutoComplete
                        label="ตำบล/แขวง"
                        options={[
                          ...new Set(
                            provinceList
                              .filter((e) => e.PostCodeMain === zipCodeCom)
                              .map((e) => e.TambonThai)
                          )
                        ]}
                        {...rest}
                        onChangeAT={(e, v) => {
                          setValue('company.tumbol', v)
                        }}
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                  <Controller
                    name="company.web"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="เว็บไซต์"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                  <Controller
                    name="company.tel"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          value={value ?? ''}
                          label="เบอร์โทร"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                  <Controller
                    name="company.email"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => (
                      <Input
                        {...rest}
                        label="Email"
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                </Fragment>
              )}
              <Box pt={4}>
                <Typography variant="h5">ข้อมูลผู้ใช้</Typography>
              </Box>
              <Controller
                name="personCardID"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="เลขประจำตัวประชาชน"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="name"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="ชื่อ"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="lastName"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="นามสกุล"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="tel"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="เบอร์โทรศัพท์"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="email"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    disabled={true}
                    label="Email"
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="gender"
                control={control}
                render={({ field: { value, ref, ...rest } }) => (
                  <RadioGroup row value={value}>
                    <Radio {...rest} label="ชาย" inputRef={ref} value="male" />
                    <Radio
                      {...rest}
                      label="หญิง"
                      inputRef={ref}
                      value="female"
                    />
                  </RadioGroup>
                )}
              />
              <Controller
                name="birthDay"
                control={control}
                render={({
                  field: { ref, value, ...rest },
                  fieldState: { error }
                }) => (
                  <DatePicker
                    label="วัน/เดือน/ปี เกิด"
                    {...rest}
                    inputRef={ref}
                    value={value}
                    onChange={(v) => {
                      if (v) {
                        setValue('birthDay', v)
                        const er: ErrorOption = {}
                        setError('birthDay', er)
                      }
                    }}
                    renderInput={(params) => (
                      <Input
                        {...params}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                )}
              />
              <Controller
                name="address"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <Input
                    {...rest}
                    label="สถานที่ติดต่อ/ที่อยู่ปัจจุบัน"
                    minRows={3}
                    multiline
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="postCode"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="รหัสไปรษณีย์"
                    options={[
                      ...new Set(provinceList.map((e) => e.PostCodeMain))
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('postCode', v)
                      setValue('province', '')
                      setValue('ampher', '')
                      setValue('tumbol', '')
                    }}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="province"
                control={control}
                render={({
                  field: { ref, value, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="จังหวัด"
                    options={[
                      ...new Set(
                        provinceList
                          .filter((e) => e.PostCodeMain === zipCode)
                          .map((e) => e.ProvinceThai)
                      )
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('province', v)
                    }}
                    value={value}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="ampher"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="อำเภอ/เขต"
                    options={[
                      ...new Set(
                        provinceList
                          .filter((e) => e.PostCodeMain === zipCode)
                          .map((e) => e.DistrictThai)
                      )
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('ampher', v)
                    }}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
              <Controller
                name="tumbol"
                control={control}
                render={({
                  field: { ref, ...rest },
                  fieldState: { error }
                }) => (
                  <AutoComplete
                    label="ตำบล/แขวง"
                    options={[
                      ...new Set(
                        provinceList
                          .filter((e) => e.PostCodeMain === zipCode)
                          .map((e) => e.TambonThai)
                      )
                    ]}
                    {...rest}
                    onChangeAT={(e, v) => {
                      setValue('tumbol', v)
                    }}
                    inputRef={ref}
                    error={!!error?.type}
                    helperText={error?.message}
                  />
                )}
              />
            </Box>
          }
          step3={
            <>
              {personType == 'company' && (
                <>
                  <Upload
                    label="แนบสำเนาหนังสือบริคณห์สนธิ"
                    success={(url) => {
                      setValue('personWithDocUrl', url)
                    }}
                  />
                  <Upload
                    label="แนบสำเนาหนังสือรับรองนิติบุคคล"
                    success={(url) => {
                      setValue('personDocUrl', url)
                    }}
                  />
                </>
              )}
              <Upload
                label="แนบบัตรประชาชน"
                success={(url) => {
                  setValue('company.companyCerUrl', url)
                }}
              />
              <Upload
                label="รูปถ่ายคู่บัตรประชาชน"
                success={(url) => {
                  setValue('company.memorandumUrl', url)
                }}
              />
            </>
          }
          onSubmit={() => {
            const data = getValues()
            onSubmit(data)
            console.log({ onSubmit: data })
          }}
          onNext={(i) => {
            setStep(i)
            trigger()
          }}
          onBack={(i) => {
            setStep(i)
            if (i === 0) {
              reset(defaultValues)
            }
          }}
        />
      </form>
    </Container>
  )
}
export default RegisterPage
