import {
  Autocomplete,
  AutocompleteChangeDetails,
  AutocompleteChangeReason,
  Box,
  InputLabel,
  TextField,
  TextFieldProps
} from '@mui/material'
import { useTheme } from '@mui/material/styles'

type AutoCompleteProp = TextFieldProps & {
  options: any[]
  onChangeAT?: (
    event: React.SyntheticEvent<Element, Event>,
    value: any,
    reason: AutocompleteChangeReason,
    details?: AutocompleteChangeDetails<any> | undefined
  ) => void
}

export default function AutoComplete({
  onChangeAT,
  value,
  ...rest
}: AutoCompleteProp) {
  const theme = useTheme()
  return (
    <Box pt={1} pb={1}>
      <InputLabel style={{ color: theme.palette.primary.dark }}>
        {rest.label}
      </InputLabel>
      <Autocomplete
        options={rest.options}
        onChange={onChangeAT}
        value={value ?? ''}
        isOptionEqualToValue={(option, value) => {
          if (value == '') {
            return true
          } else if (option != value) {
            return false
          } else {
            return true
          }
        }}
        renderInput={(params) => (
          <TextField
            {...rest}
            {...params}
            label=""
            variant="outlined"
            color="secondary"
            size="small"
            fullWidth
          />
        )}
      />
    </Box>
  )
}
