import { FormControlLabel, Radio as MyRadio, RadioProps } from '@mui/material'

interface RadioProp extends RadioProps {
  label?: string
}

export default function Radio(prop: RadioProp) {
  return (
    <FormControlLabel
      value={prop.value}
      control={<MyRadio {...prop} color="secondary" />}
      label={prop.label}
    />
  )
}
