import { Box, Button, Typography } from '@mui/material'
import { Stack } from '@mui/system'
import { getDownloadURL, getStorage, ref, uploadBytes } from 'firebase/storage'
import { ChangeEvent, useRef, useState } from 'react'

export default function Upload({
  success,
  label
}: {
  label: string
  success: (url: string) => void
}) {
  const hiddenFileInput = useRef(null)
  const [filePath, setFilePath] = useState<string>('')

  const handleChange = async (event: ChangeEvent<HTMLInputElement>) => {
    const fileUploaded = event.target.files as any
    const file = fileUploaded[0] as File

    const storage = getStorage()
    const storageRef = ref(storage, `images/${file.name}`)

    const snapshot = await uploadBytes(storageRef, file)
    const data = await getDownloadURL(snapshot.ref)
    setFilePath(data)
    success(data)
  }

  const handleClick = () => {
    const current = hiddenFileInput.current as any
    current.click()
  }

  return (
    <Box pb={2} pt={2}>
      <Typography variant="subtitle1">{label}</Typography>
      <input
        type="file"
        style={{ display: 'none' }}
        ref={hiddenFileInput}
        onChange={handleChange}
      />
      <Stack direction="row" spacing={1}>
        <Button onClick={handleClick}>
          <img src="/images/upload.png" height="50px" />
        </Button>
        {filePath && <img src={filePath} height="50px" />}
      </Stack>
    </Box>
  )
}
